article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block;
}

audio, canvas, video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden] {
  display: none;
}

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

a:focus {
  outline: thin dotted;
}

a:active, a:hover {
  outline: 0;
}

h1 {
  font-size: 2em;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b, strong {
  font-weight: 700;
}

dfn {
  font-style: italic;
}

mark {
  background: #ff0;
  color: #000;
}

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em;
}

pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

q {
  quotes: \201C \201D \2018 \2019;
}

small {
  font-size: 80%;
}

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -.5em;
}

sub {
  bottom: -.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em;
}

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}

button, input {
  line-height: normal;
}

button, html input[type=button],
/* 1 */

input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled], input[disabled] {
  cursor: default;
}

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body, figure {
  margin: 0;
}

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0;
}

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.visuallyHidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(00 0 0);
  overflow: hidden;
}

h1, h2, p, body, header, ul, section, nav, a {
  padding: 0;
  margin: 0;
}

html {
  font-size: 62.5%;
  font-family: 'Open Sans', sans-serif;
  color: #ffeae2;
}

body {
  background-color: #2c2c2c;
  height: 100vh;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.logo {
  margin-top: 40px;
  text-align: center;
  font-size: 4rem;
  color: #9bcadd;
}

img {
  max-width: 100%;
}

.wrapper {
  max-width: 1080px;
  width: 80%;
  margin: 0 auto;
}

h1 {
  font-family: 'Anton', sans-serif;
  font-size: 6rem;
  text-align: center;
  padding: 10px 0px;
}

button {
  background-color: #012533;
  border: 2px solid #9bcadd;
  color: #ffeae2;
  padding: 10px 20px;
  margin-right: 20px;
  margin-top: 20px;
  font-size: 2.2rem;
}

button:hover, button:focus {
  background-color: #006400;
}

input {
  border: 2px solid #012533;
  padding: 5px 2px;
}

.intro {
  font-size: 3.2rem;
  text-align: center;
}

.subIntro {
  font-size: 2.2rem;
  margin-top: 20px;
}

.howTo {
  font-weight: bold;
}

.journal {
  margin: 50px auto;
  padding: 30px 20px;
  background-color: #067cab;
  border: 4px solid #ffeae2;
}

.journal p, .date, .log p {
  margin-top: 20px;
  font-size: 2.5rem;
  font-weight: bold;
}

.large {
  width: 45%;
  margin: 10px 40px 10px 0px;
}

.small {
  width: 8%;
  margin: 0px 10px;
}

.logsContainer {
  column-count: 3;
  column-gap: 2;
  display: block;
}

.log {
  background-color: #ffeae2;
  color: #067cab;
  border: 4px solid #067cab;
  padding: 10px 20px;
  margin-bottom: 10px;
  page-break-inside: avoid;
  break-inside: avoid-column;
}

.log button {
  margin: 20px 0px;
}

.log li {
  padding: 5px 0px;
}

.log button {
  padding: 10px 10px;
  font-size: 2rem;
}

.log p, .highlite {
  color: #012533;
}

.highlite, .highliteEx {
  font-weight: bold;
}

.highliteEx {
  color: #330f01;
}

select {
  width: 50%;
}

@media(max-width:900px) {
  .logsContainer {
    column-count: 2;
  }
}

@media(max-width:820px) {
  .large {
    width: 40%;
    margin-right: 25px;
  }
}

@media(max-width:660px) {
  h1 {
    font-size: 5rem;
  }
  .intro {
    font-size: 2.5rem;
  }
  .logsContainer {
    column-count: 1;
  }
  .large {
    width: 100%;
  }
  .small {
    width: 16%;
    margin-left: 0px;
    margin-bottom: 15px;
  }
  label {
    margin-right: 5px;
  }
}

@media(max-width:500px) {
  h1 {
    font-size: 4rem;
  }
}